import React from 'react';
import moment from 'moment';
import {
  Form,
  Input,
  Select,
  DatePicker, Radio,
} from 'antd';
import DeliveryMethod from "./components/DeliveryMethod";

const OrderFields = ({ form }) => {
  return (
    <>
      <Form.Item
        noStyle
        shouldUpdate
      >
        {() => {
          return (
            <DeliveryMethod
              form={form}
            />
          )
        }}
      </Form.Item>
      <Form.Item dependencies={["deliveryMethodId"]} noStyle>
        {() => {
          const deliveryMethodId = form.getFieldValue("deliveryMethodId");
          return (deliveryMethodId === 2 || deliveryMethodId === 3) &&
            <Form.Item
              name="deliveryAddress"
              label=" "
              rules={[
                {
                  required: true,
                  message: ' ',
                  whitespace: true,
                },
              ]}
            >
              <Input/>
            </Form.Item>
        }}
      </Form.Item>
      <Form.Item
        name="deliveryDate"
        label=" "
        rules={[
          {
            required: true,
            message: '  ',
          },
        ]}
      >
        <DatePicker
          format="DD.MM.YYYY"
          disabledDate={(current) => {
            if (current) {
              if (current.isBefore(moment("2022-07-05"))) {
                return true;
              }
              if (current.isSame(new Date(), "day")) {
                return false;
              }
              return current < moment().endOf('day');
            }
            return false;
          }}
        />
      </Form.Item>
      <Form.Item
        name="paymentMethod"
        label=" "
        rules={[
          {
            required: true,
            message: '  ',
          },
        ]}
      >
        <Radio.Group>
          <Radio value=""></Radio>
          <Radio value=""></Radio>
        </Radio.Group>
      </Form.Item>
    </>
  );
};

export default OrderFields;